// css loading

.page-loading {
    @include fullscreen(true);
    background-color: #131313;
    z-index: 999;
    opacity: 1;
    &__logo {
        @include mid;
        opacity: 0;
    }
    &__text {
        @include mid;
        text-align: center;
        text-transform: uppercase;
        opacity: 0;
        .--h5 {
            margin-bottom: 18px;
            color: $white60-cl;
        }
        .--h2 {
            color: $gold-cl;
        }
    }
    &.is-loaded {
        pointer-events: none;
        opacity: 0;
    }
}